<template>
  <v-container grid-list-md>

    <v-layout row wrap>
      <v-flex xs3 v-for="movie in movies" :key="movie.Id">
        <v-container>
          <movie-tile :movie="movie"></movie-tile>
        </v-container>
    </v-flex>
  </v-layout>

    <!--<v-fab-transition>-->
      <!--<v-btn v-show="hidden" color="red darken-2" @click="handleRent()" large fab fixed bottom right icon>-->
          <!--<v-icon>shopping_cart</v-icon>-->
      <!--</v-btn>-->
    <!--</v-fab-transition>-->

    <v-snackbar v-model="snackbar" :top="true" :timeout="6000" color="dark">
      <v-layout justify-center align-center fill-height>{{ snackbar_text }}</v-layout>
    </v-snackbar>

    <v-snackbar v-model="queueBar" auto-height :vertical="true" :timeout="0" :bottom="true" :left="true" color="red darken-4">
      <v-layout justify-center align-center fill-height class="headline mb-3">Movies in Cart</v-layout>
      <v-layout v-for="movie in queue" :key="movie.Id">
          {{ movie.Name + '\t'}} ${{ movie.Price }}
      </v-layout>
      <v-divider class="subheading mt-3"></v-divider>
      <div class="subheading mt-3">Total: ${{quetotal}}</div>
      <div style="border-width: 2px; border-color: #fff">
        <router-link to="/home">Checkout Now <v-icon> arrow_forward</v-icon></router-link>
      </div>
    </v-snackbar>

  </v-container>
</template>

<script>
  import Model from '../Model'
  import MovieTile from "./MovieTile";

  export default {
    components: {
      MovieTile,
    },
    data() {
      return {
        movies: null,
        subColor: "#ab302f",
        badge_count: 0,
        hidden: false,
        snackbar: false,
        snackbar_text: null,
        queueBar: false,
        queue: [],
        timeout: 0,
        quetotal: null,
      };
    },
    beforeMount() {
      this.movies = [];

      this.$on('movie_added', (movie) => {
        if(this.queue.filter(qMovie => {return (qMovie.Name === movie.Name)}).length === 0){
          this.queue.push(movie);
          this.snackbar = true;
          this.snackbar_text = "You added " + movie.Name + " to the cart";
          this.quetotal = this.getQueTotal();
          this.handleRent()
        } else {
          this.snackbar = true;
          this.snackbar_text = "You removed " + movie.Name + " from the cart";
          this.queue.splice(this.queue.indexOf(movie),1);
          if(this.queue.length === 0){
            this.queueBar = false;
          }
          this.handleRent()
        }
      })

    },
    mounted(){
     this.movies = JSON.parse(localStorage.MoviesDB);
     // this.initDefault()
    },
    methods: {
      initDefault(){
        Model.Kiosk.New_Movie_From_API(1,"Frozen",15,"12/12/19").then( (movie) => {
          this.movies.push(movie);
        });
        Model.Kiosk.New_Movie_From_API(2,"Ponyo",20,"1/03/19").then( (movie) => {
          this.movies.push(movie);
        });
        Model.Kiosk.New_Movie_From_API(3,"Inception",25,"4/06/19").then( (movie) => {
          this.movies.push(movie);
        });
        Model.Kiosk.New_Movie_From_API(4,"Transformers",20,"4/06/19").then( (movie) => {
          this.movies.push(movie);
        });
        Model.Kiosk.New_Movie_From_API(5,"Sherlock Holmes",5,"4/06/19").then( (movie) => {
          this.movies.push(movie);
        });
        Model.Kiosk.New_Movie_From_API(6,"Dorian Gray",30,"4/06/19").then( (movie) => {
          this.movies.push(movie);
        });
        Model.Kiosk.New_Movie_From_API(7,"Faustus",15,"4/06/19").then( (movie) => {
          this.movies.push(movie);
        });
        Model.Kiosk.New_Movie_From_API(8,"Beach Rats",10,"4/06/19").then( (movie) => {
          this.movies.push(movie);
        });
        Model.Kiosk.New_Movie_From_API(9,"Those People",25,"4/06/19").then( (movie) => {
          this.movies.push(movie);
        });
        Model.Kiosk.New_Movie_From_API(10,"Riverdale",20,"4/06/19").then( (movie) => {
          this.movies.push(movie);
        });
        Model.Kiosk.New_Movie_From_API(11,"Simpsons",25,"4/06/19").then( (movie) => {
          this.movies.push(movie);
        });
        Model.Kiosk.New_Movie_From_API(12,"Volare",25,"4/06/19").then( (movie) => {
          this.movies.push(movie);
        });
        Model.Kiosk.New_Movie_From_API(13,"Howls Castle",25,"4/06/19").then( (movie) => {
          this.movies.push(movie);
        });
        Model.Kiosk.New_Movie_From_API(14,"Aladdin",25,"4/06/19").then( (movie) => {
          this.movies.push(movie);
        });
        Model.Kiosk.New_Movie_From_API(15,"The Lego Movie",15,"4/06/19").then( (movie) => {
          this.movies.push(movie);
        });
      },
      handleRent(){
        // var active = Model.Customer.getActiveUser();
        this.queueBar = true;
        this.quetotal = this.getQueTotal();
        if(this.queue.length === 0){
          this.queueBar = false;
        }
        // this.queue.forEach(movie => {
        //   Model.Kiosk.RentMovie(movie,active)
        // });
      },
      getQueTotal(){
        var count = 0;
        this.queue.forEach(e => {
          count += parseInt(e.Price);
        });
        return count;
      }
    }
  };
</script>

<style scoped>

  a {
    color: #fff;
    text-decoration: none;
    font-size: 18px;
  }

  #form {
    margin-right: 5%;
    margin-left: 5%;
    padding-top:10px;
    padding-bottom:30px;
    text-align: left;

  }

  .v-btn--large{
    padding: 10px !important;
  }

  .header {
    background-color: rgba(185, 134, 12, 0.79);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
</style>



