<template>
  <v-layout justify-center fill height>

      <v-card :color="cardColor" elevation="10" width="400">
          <v-card-title><v-layout justify-center class="headline mt-3">Movie Store Login - Deployed</v-layout></v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12>
                  <v-text-field v-model="email" :color="buttonColor" label="Email" required></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field v-model="password" :color="buttonColor" label="Password" type="password" required></v-text-field>
                </v-flex>
                <v-flex xs12 >
                  <router-link to="/register"><em><small>Don't have an account? Register here</small></em></router-link>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn :color="buttonColor" absolute bottom right fab v-on:keyup.13="handleLogin()" @click="handleLogin()"><v-icon>done</v-icon></v-btn>
          </v-card-actions>
      </v-card>

    <v-dialog width="300" v-model="loadingDialog" persistent>
      <v-card :color="buttonColor" dark>
        <v-card-text>
          One Moment Please...
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog width="300" v-model="updateDialog">
      <v-card :color="buttonColor" dark>
        <v-card-text>
          User Not Found.
        </v-card-text>
      </v-card>
    </v-dialog>

  </v-layout>
</template>

<script>

  import Model from '../Model.js'

  export default {
    data() {
      return {
        buttonColor: "#ab302f",
        cardColor: "#404040",
        email: "",
        password: "",
        people: null,
        loadingDialog: false,
        updateDialog: false
      };
    },
    methods: {
      handleLogin(){
        this.loadingDialog = true;
        var transmission = Model.Customer.authenticate(this.email,this.password);
        console.log(transmission);
        if(transmission.authorised){
          Model.Customer.setActiveUser(transmission.user);
          setTimeout(() => {this.loadingDialog = false;}, 2000);
          this.$router.push('/home/')
        } else {
          setTimeout(() => {this.loadingDialog = false;}, 1000);
          setTimeout(() => {this.updateDialog = true;}, 1000);
        }
      }
    }
  };
</script>

<style scoped>

  a {
    color: #dc303f;
    text-decoration: none;
    font-size: 18px;
  }

  #form {
    margin-right: 5%;
    margin-left: 5%;
    padding-top:10px;
    padding-bottom:30px;
    text-align: left;
  }

  .v-btn--large{
    padding: 10px !important;
  }

  .header {
    background-color: rgba(185, 134, 12, 0.79);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
</style>



