<template>
  <v-layout justify-center>
      <v-card color="red darken-4" elevation="10" width="900">
        <v-card-title><v-layout justify-center class="headline mt-3">{{ActiveUser.First_name}}'s Profile</v-layout></v-card-title>
        <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12 sm6 md56>
                  <v-text-field v-model="fname" :color="buttonColor" label="First name" required></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md6>
                  <v-text-field v-model="lname" :color="buttonColor" label="Last name" required></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field v-model="email" :color="buttonColor" label="Email" required></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field v-model="password" :color="buttonColor" label="Password" type="password" required></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn :color="buttonColor" absolute bottom right fab @click="addUser()"><v-icon>done</v-icon></v-btn>
          </v-card-actions>
      </v-card>
  </v-layout>
</template>

<script>

  import Model from '../Model.js'

  export default {
    data() {
      return {
        buttonColor: "#ab302f",
        cardColor: "red darken-4",
        fname: "",
        lname: "",
        email: "",
        password: "",
        age: "",
      };
    },
    props: {
      ActiveUser: Object
    },
  };
</script>

<style scoped>
  #form {
    margin-right: 5%;
    margin-left: 5%;
    padding-top:10px;
    padding-bottom:30px;
    text-align: left;
  }

  .v-btn--large{
    padding: 10px !important;
  }

  .header {
    background-color: rgba(185, 134, 12, 0.79);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
</style>



