<template>
  <v-card color="red darken-4"  class="white--text">
    <v-layout row>
      <v-flex xs8>
        <v-card-title primary-title>
          <div>
            <div class="headline">{{movie.Name}}</div>
            <div>{{movie.Year}}</div><br>
            <div>{{movie.Description}}</div><br>
            <div>Price: ${{movie.Price}}</div>
            <div>Next Session : {{new Date(movie.Sessions[0]).toDateString()}}</div>
          </div>
        </v-card-title>
      </v-flex>
      <v-flex xs5>
        <v-img
          :src="movie.Poster"
          height="510px"
          contain
        ></v-img>
      </v-flex>
    </v-layout>
    <v-divider light></v-divider>
    <v-card-actions class="pa-3">
      <v-btn fab left><v-icon color="green">thumb_up</v-icon></v-btn>
      <v-btn fab right style="margin-left:40px"><v-icon color="red">thumb_down</v-icon></v-btn>
      <v-spacer></v-spacer>
      <div v-for="i in movie.Rating">
        <v-icon color="yellow">star_border</v-icon>
      </div>
      <div v-for="i in (5-movie.Rating)">
        <v-icon>star_border</v-icon>
      </div>
    </v-card-actions>
  </v-card>
</template>

<script>

  import Model from '../Model'

    export default {
        name: "MovieCard",
        beforeMount(){
          console.log(Model.Kiosk.getMovieById(this.selectedId));
          this.movie = Model.Kiosk.getMovieById(this.selectedId);
        },
      data(){
          return {
            movie: {}
          }
      },
      props:{
          color: String,
          selectedId: String
      }
    }
</script>

<style scoped>

</style>



